.tarea-contenedor {
     width: 500px;
     min-height: 65px;
     background-color: rgb(40, 4, 41);
     margin: 5px 0;
     padding: 8px 15px 8px 20px;
     display:flex;
     justify-content: space-between;
     align-items: center;
     border-radius: 7px;
     border: 1px solid white;
     cursor: pointer;
}
/* Colores */
.tarea-contenedor:nth-child(3n + 1) {
    background-color: rgb(40, 4, 41);
}
.tarea-contenedor:nth-child(3n + 2) {
    background-color: rgb(65, 8, 68);
}
.tarea-contenedor:nth-child(3n + 3) {
    background-color: rgb(106, 15, 110);
}
.tarea-contenedor.completada {
    text-decoration: line-through;
    background-color: rgb(235, 190, 42);
}
/* Texto */
.tarea-texto {
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
}

/* Icono */
.tarea-contenedor-iconos {
    display: flex;
}
.tarea-icono { 
    width: 25px;
    height: 25px;
    justify-content: center;
    align-items: center;
}